#whatidoStasti {
  margin-bottom: 50px;
  /* border: 2px solid rgb(30, 0, 255); */
  width: 98%;
  margin: auto;
}

.whatidoStasti-title {
  color: var(--txtcolor);
  text-align: center;
  margin-bottom: -80px;
}

.fullstack {
  font-size: 40px;
  font-weight: 501;
  margin-top: 50px;
  color: var(--txtcolor);
}
#statCircle {
  margin-bottom: 100px;
  /* border: 1px solid red; */
  margin: auto;
  margin-left: 50px;
}
#statCala {
  margin-left: 300px;
}
#gitStat {
  margin-top: 100px;
}
.row {
  display: flex;
  margin-bottom: 100px;
}
.coding {
  /* border: 1px solid rgb(8, 14, 0); */
  width: 250px;
  height: 250px;
  margin-top: 100px;

  text-align: center;
}
.circle {
  border: 10px solid blueviolet;
  width: 200px;
  height: 200px;
  border-radius: 50%;
}
.innerCircle {
  margin-top: 60px;
  font-size: 45px;
  color: #d712b6;
}

.circle-title {
  color: black;
  margin-top: 20px;
  text-align: center;
}
.circle-subtitle {
  color: black;
  text-align: center;
}

.img-fluid {
  margin-top: 100px;
}
.col-lg-5 {
  text-align: center;
}
.i {
  padding: 40px 25px 0px 25px;
}
.center {
  text-align: center;
  align-items: center;
  padding-left: 0;
}

.tags {
  display: inline;
  position: relative;
}

div.logo {
  margin-right: 95px;
  margin-left: 95px;
}
.fluid-container {
  margin-top: -15px;
}
.Calander {
  margin-left: 300px;
}

@media (max-width: 950px) {
  .img-fluid {
    margin-top: 30px;
    margin-bottom: 20px;
  }
  .tags:hover:after {
    bottom: -23px;
  }
  .tags:hover:before {
    bottom: 10px;
  }
  .Whatido-title {
    font-size: 50px;
  }

  .fullstack {
    margin-top: 20px;
    margin-bottom: 20px;
  }
  .center .i-mobile {
    padding: 40px 25px 15px 25px;
  }
  div.logo {
    margin-right: 200px;
    margin-left: 200px;
  }
  .rowcircle {
    width: 100px;
    margin-left: 200px;
  }
  .leftcoding {
    margin-left: 0px;
  }
  .Calander {
    margin-top: 200px;
    margin-left: -200px;
  }
  .circle-title {
    margin-left: 80px;
  }
  .circle-subtitle {
    margin-left: 80px;
    width: 80px;
  }
  #statCala {
    margin-left: 50px;
    width: 700px;
  }
  #gitgraph {
    width: 100%;
  }
}

@media (max-width: 450px) {
  div.logo {
    margin-right: 100px;
    margin-left: 100px;
  }
  #whatido p {
    text-align: left;
  }
  .i {
    padding: 20px 0px 0px 0px;
  }
  .rowcircle {
    width: 100px;
    margin-left: -30px;
  }
  .leftcoding {
    margin-left: 0px;
  }
  .Calander {
    margin-top: 200px;
    margin-left: -200px;
  }
  .circle-title {
    margin-left: 80px;
  }
  .circle-subtitle {
    margin-left: 80px;
    width: 80px;
  }
  #statCala {
    margin-left: 5px;
    width: 370px;
  }
  #gitstat {
    width: 90%;
  }
  #gitgraph {
    width: 100%;
  }
  .whatidoStasti-title {
    font-size: 30px;
  }
}
